<template>
  <div class="demo1">
    <h1>CVAE 生成数字</h1>
    <div class="img-back">
      <img :src="img" alt="" />
    </div>
    <div class="bt-group">
      <div
        class="bt"
        :class="item.isActive ? 'active' : ''"
        v-for="(item, index) in buttons"
        v-bind:key="item.value"
        @click="selectNum(index)"
      >
        {{ item.value }}
      </div>
    </div>
  </div>
</template>

<script>
import("../style/demo.css");

export default {
  name: "demo1",
  props: {
    msg: String,
  },
  data() {
    return {
      img: "",
      buttons: [
        {
          value: 1,
          isActive: true,
        },
        {
          value: 2,
          isActive: false,
        },
        {
          value: 3,
          isActive: false,
        },
        {
          value: 4,
          isActive: false,
        },
        {
          value: 5,
          isActive: false,
        },
        {
          value: 6,
          isActive: false,
        },
        {
          value: 7,
          isActive: false,
        },
        {
          value: 8,
          isActive: false,
        },
        {
          value: 9,
          isActive: false,
        },
        {
          value: 0,
          isActive: false,
        },
      ],
    };
  },
  methods: {
    selectNum(index) {
      console.log(index);
      this.buttons = this.buttons.map((v, i) => {
        v.isActive = false;
        if (i == index) {
          v.isActive = true;
        }
        return v;
      });
      this.axios
        .post(
          "/cvaeMnist",
          {
            label: this.buttons[index].value,
          },
          {
            Headers: { "Content-Type": "application/json" },
          }
        )
        .then((res) => {
          console.log(res);
          this.img = "data:image/png;base64," + res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

